.Homevideo1 {
  width: 100%;
}
.Homevideo2 {
  width: 100%;
}
.herotext {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.FixedButton {
  position: fixed;
  top: 50%;
  left: 5%;
  transform: translate(-50%, -50%);
  color: white;
  z-index: 1000;
}
.helpcenter {
  position: fixed;
  bottom: 0%;
  right: 0%;
  transform: translate(-50%, -50%);
  color: white;
  z-index: 1000;
}
.map {
  background-color: black;
}
.room {
  transition: 0.4s linear;
  -webkit-transition: 0.4s linear;
  -moz-transition: 0.4s linear;
  -ms-transition: 0.4s linear;
  -o-transition: 0.4s linear;
  position: relative;
}
.room:hover {
  box-shadow: 0px 9px 18px rgba(0, 0, 0, 0.5) !important;
}

.price-top {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  color: var(--mainWhite);
  padding: 0.3rem 0.6rem 0.5rem;
  border-bottom-right-radius: 1rem;
  font-size: 0.5rem;
  text-align: center;
  transition: 0.4s linear;
  -webkit-transition: 0.4s linear;
  -moz-transition: 0.4s linear;
  -ms-transition: 0.4s linear;
  -o-transition: 0.4s linear;
}
.price-top h6 {
  margin-bottom: 0;
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: var(--mainSpacing);
}
.room-link {
  position: absolute;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: all 0.3s linear;
}
.room:hover {
  background: rgba(0, 0, 0, 0.3);
}
.room:hover img {
  opacity: 0.3;
}
.room:hover .price-top {
  opacity: 0;
}
.room:hover .room-link {
  opacity: 1;
  transform: translate(-90%, -200%) scale(1);
  -webkit-transform: translate(-90%, -200%) scale(1);
  -moz-transform: translate(-90%, -200%) scale(1);
  -ms-transform: translate(-90%, -200%) scale(1);
  -o-transform: translate(-90%, -200%) scale(1);
}
.room-info {
  background: #f2f3f4;
  text-transform: capitalize;
  padding: 0.5rem 0;
  text-align: center;
  font-weight: 700;
  letter-spacing: var(--mainSpacing);
  margin-bottom: 0;
}
.room-Data {
  background: #f2f3f4;
}
.main a {
  text-decoration: none;
  font-weight: 600;
  font-size: large;
  color: red;
}
.main a:hover {
  color: rgb(34, 37, 180);
  font-weight: 700;
}
.Check {
  background-color: black;
  width: 100%;
}
.Footer {
  background-color: black;
}
.header__image {
  width: 90px;
}
.Search__Bar {
  background-color: black;
}

@media screen and (max-width: 992px) {
  .herotext {
    top: 40%;
    left: 50%;
  }
}
@media screen and (max-width: 768px) {
  .herotext {
    top: 30%;
    left: 50%;
  }
}
@media screen and (max-width: 576px) {
  .header__image {
    width: 50px;
  }
  .herotext {
    top: 40%;
    left: 50%;
    font-size: 10px;
  }
  /* .herotext h1 {
    font-size: 0px;
  } */
  .Locationtext {
    font-size: 20px;
  }
  .dicoverText {
    font-size: 20px;
  }
  .FixedButton {
    left: 10%;
    opacity: 80%;
  }
  .Homevideo1 {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .Homevideo2 {
    display: none;
  }
  .herotext {
    display: none;
  }
  .Crousal2 {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .Crousal1 {
    display: none;
  }
  .room {
    margin: 0 30px;
  }
}
