.Bannerr {
  height: 300px;
  position: relative;
}

.Bannerr .Mediaa {
  background-color: white;
  height: 100%;

  overflow: hidden;
  position: relative;
  -webkit-transition: 300ms;
  transition: 300ms;
  cursor: pointer;
}

.Bannerr .Mediaa .MediaaCaption:hover {
  opacity: 0.8;
}
.Mediaacheck {
  object-fit: cover;
}

/* .Bannerr .Mediaa:hover {
  -webkit-filter: brightness(115%);
  filter: brightness(115%);
} */

.Bannerr .BannerrGrid {
  height: 100%;
  position: relative;
}
